import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

declare interface User {
  id: number;
  username: string;
}

export default new Vuex.Store({
  state: {
    user: {} as User,
    API_URL: '',
  },
  mutations: {
    setUser: (user: any, state) => {
      state.user.id = user.id;
      state.user.username = user.username;
    },
  },
  actions: {

  },
});
